import styled from "styled-components";

export default function Title({children}) {
    return (
        <TitleWrapper>{children}</TitleWrapper>
    )
}
const TitleWrapper = styled.div`
  color: #219653;
  font-size: 28px;
  font-weight: bold;
  font-style: normal;
  text-align: center;
  margin: 1em 0;
  display: block;
  position: relative;
  width: 100%;
`;